.app-header {
    background-color: #282c34;
    padding: 10px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-link {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
}

.nav-link:hover {
    color: #61dafb;
}

.nav-link.active {
    color: #61dafb;
    font-weight: bold;
    border-bottom: 2px solid #61dafb;
}